.DateRangePicker {
  width: 100%;
}

.DateInput {
  width: 45%;
}

.DateInput_input {
  color: #9a9a9a;
  text-align: center;
}

.DateInput_input::placeholder {
  color: #9a9a9a;
}

.DateInput input[type="search"],
.DateInput input[type="text"] {
  padding: 1rem;
  font-size: 1.6rem;
  font-weight: 500;
  border: none;
}

.DateInput input[type="text"]:focus {
  border-bottom: 2px solid #3ec5cb;
}

.DateRangePickerInput__withBorder {
  border: 2px solid #e6e6e6;
  border-radius: 0;
}

.DateRangePickerInput_arrow {
  width: 10%;
}

.DateRangePickerInput_arrow_svg {
  fill: #9a9a9a;
}

.DateRangePicker_picker {
  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.4);
  z-index: 4;
}

.DayPicker__withBorder {
  border-radius: 0;
}
